import React from "react"
import { graphql } from "gatsby"
import {Trans, useTranslation} from "gatsby-plugin-react-i18next"
import Layout from "../components/Layout"

const Portfolio = ({data}) => {

  const projects = data.site.siteMetadata.projects

  const {t} = useTranslation() 

  return (
    <Layout page="PortfolioPage" title={t("PORTFOLIO")}>
      <TimeLine projects={projects} />
    </Layout>
  )
}

const TimeLine = ({ projects }) => (
  <main id="portfolio">
    <section className="timeline">
      {projects.map(project => (
        <div className={project.className} key={project.name}>
          <div className="content">
            <h2>
              {project.name} (<Trans>{project.date}</Trans>)
            </h2>
            <p><Trans>{project.desc}</Trans></p>
          </div>
        </div>
      ))}
    </section>
  </main>
)

export const portfolioQuery = graphql`
  query {
    site {
      siteMetadata {
        projects {
          name
          date
          desc
          className
        }
      }
    }
  }
`

export default Portfolio
